.products {
  padding-top: 60px;
  padding-bottom: 60px;
  background: #FFFFFF;
}

.prod-title-natural {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.16;
  text-align: center;
  text-transform: uppercase;

  color: #FFA5BA;

  margin-bottom: 15px;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 30px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 34px;
  }
}

.prod-title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: $title-text-color;

  margin-bottom: 148px;
  font-family: 'Titan One';
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 39px;
  
  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 55px;
    margin-bottom: 131px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 66px;
    letter-spacing: .06em;
    margin-bottom: 217px;
  }
}

.products-list {
  list-style: none;
  margin: 0;
  padding: 0;

  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.products-list__contain {
  text-align: center;
  border-radius: 24px;
  padding: 160px 40px 40px;
  position: relative;
  width: 280px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    width: calc(( 100% - 32px ) / 2);
    padding: 144px 32px 40px;

    &:not(:last-child) {
      margin-right: 16px;
    }
  }

  @media screen and (min-width: 1200px) {
    padding: 229px 63px 40px;

    &:not(:last-child) {
      margin-right: 32px;
    }
  }
}

.cream {
  background-color: #FFB8CA;
}

.crem::before {
  position: absolute;
  top: -140px;
  transform: translateX(-50%);
  left: 50%;
  width: 280px;
  content: '';
  display: block;
  height: 280px;
  background-image: url('../images/products-pic1-mobile.png');
  background-size: contain;
  background-repeat: no-repeat;

  @media screen and (min-width: 768px) {
    top: -112px;
    width: 224px;
    height: 224px;
    background-image: url('../images/products-pic1-tablet.png');
  }

  @media screen and (min-width: 1200px) {
    top: -197px;
    width: 368px;
    height: 394px;
    background-image: url('../images/products-pic1-desktop.png');
  }
}

