.prod-title__list {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #FFFFFF;

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 34px;
  }
}

.prod-title__list::after {
  display: block;
  padding-left: 4px;
  content: '';
  height: 6px;
  background-image: url('../images/Group 4.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 0%;

  margin-top: 20px;
}

.prod-text {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;

  text-align: center;
  letter-spacing: 0.04em;

  color: #FFFFFF;

  margin-top: 32px;
  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    line-height: 25px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    margin-bottom: 60px;
  }
}

.products__btn-arrow-right {
  width: 40px;
  height: 40px;

  border-radius: 50px;
  background-size: contain;
  border-style: hidden;
  background-image: url( ../images/arrow-tablet.png );

  @media screen and (min-width:1200px) {
    background-image: url( ../images/arrow-desktop.png );
  }
}

// /*====== backdrop-btn ========*/

.button {
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
              background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  border: 1px solid transparent;
  cursor: pointer;

  &:hover,
  &:focus {
    color: #ffffff;
    background-color: #D41443;
    opacity: 0.8;
  }
}

.products-modal-close-icon {
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  .modal-close-btn:hover &,
  .modal-close-btn:focus & {
    fill: var(--accent-color);
  }
}

.btn-products {
  margin-left: auto;
  margin-right: auto;
}
.modal-btn {
  display: block;
  margin: 0 auto;
}

/*====== backdrop ========*/
.products-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
              visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

  background-color: rgba(0, 0, 0, 0.2);
}

.products-backdrop.prod-cream {
  opacity: 0;
  visibility: hidden;

  pointer-events: none;
}

.products-backdrop.prod-coffee {
  opacity: 0;
  visibility: hidden;

  pointer-events: none;
}

.products-backdrop.prod-milkshakes {
  opacity: 0;
  visibility: hidden;

  pointer-events: none;
}

.products-modal-cream {
