.footer-section {
  background-color: $background-about-color;
  }

.container--footer {
  @include tablet {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 40px;
  }
}

.footer-titles {   
  display: flex;
  flex-direction: column;
  row-gap: 16px;

  width: 234px;
  margin-right: auto;
  margin-bottom: 40px;

  @include tablet {
    width: 340px;
    margin-bottom: 0;
    row-gap: 12px;
  }

  @include desktop {
    width: 480px;
    row-gap: 16px;
  }
}

.footer__first-title {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: calc(21/18);
  text-transform: uppercase;
  color: $hero-ellipse-color;

  @include tablet {
    font-size: 26px;
    line-height: calc(30/26);
    letter-spacing: 0.04em;
  }

  @include desktop {
    font-size: 30px;
    line-height: calc(34/30);
  }
}

.footer__second-title {
  font-style: normal;
  font-weight: 400;
  font-size: 34px;
  line-height: calc(39/34);
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $title-text-color;

  @include tablet {
    font-size: 48px;
    line-height: calc(55/48);
    letter-spacing: 0.06em;
  }
  
  @include desktop {
    font-size: 58px;
    line-height: calc(66/58);
  }
}

.footer-contacts {
  display:flex;
  flex-direction: column;
  flex-wrap: wrap;
  row-gap: 40px;
  align-items: flex-end;
  margin-bottom: 40px;

  @include tablet{
    margin-bottom: 19px;
  }

  @include desktop {
    margin-bottom: 0;
  } 
}

.footer-social-icon-list {
  display: flex;
  column-gap: 28px;

  @include tablet {
    column-gap: 34px;
  }
}

.social-icon-area {
  width: 32px;
  height: 32px;

  @include tablet {
    width: 34px;
    height: 34px;
  }
}

.social-link-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.social-icon {
  width: 32px;
  height: 32px;
  fill:  $hero-ellipse-color;

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  .social-link-footer:hover &,
  .social-link-footer:focus & {
    fill: $title-text-color;
  }

  @include tablet {
    width: 34px;
    height: 34px;
  }
}

.footer-tel-link {
  display: block;
  margin-left: auto;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: calc(22/12);
  color: $first-text-color;

  @include tablet {
    font-size: 14px;
    line-height: calc(26/14);
  }
}

.footer-tel-color {
  color: $title-text-color;
}

.copyrights-title {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 16px 94px 3px;
  max-width: 1200px;  
  width: 100vw;  
  font-family: DM Sans;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: calc(21/14);
  text-align: center;
  color: $second-text-color;
  border-top: 1px solid #d9d9d9;

  @media screen and (min-width: 480px) {
    padding: 8px 0px;;
  }
  @include tablet {
    line-height: calc(26/14); 
    letter-spacing: 0.02em;
  }

  @include desktop {
    padding:  7px 0px;
  }
}

.break-space {
  @media screen and (min-width: 480px) {
    display: none;
  }
}